transform: scale(1.3);\n    }\n        \n    .header_burger_container {\n\n        // #second, #third {\n        //     transition: all 0.7s ease-in-out;\n        // }\n\n        \n\n        li {\n            border-top: 1.62px $font_global-white solid;\n            height: calc(1.62px + 4px);\n            \n            \n            \n\n        }\n\n    }\n\n\n}\n\n.rightShift {\n    animation: rightShift;\n    animation-duration: 0.45s;\n}\n\n@keyframes rightShift {\n    0%, 100% {\n        transform: translateX(0);\n    }\n    10%, 90% {\n        transform: translateX(10%);\n    }\n    20%, 80% {\n        transform: translateX(20%);\n    }\n    40%, 60% {\n        transform: translateX(50%);\n    }\n}\n\n.leftShift {\n    animation: leftShift;\n    animation-duration: 0.45s;\n}\n\n@keyframes leftShift {\n    0%, 100% {\n        transform: translateX(0);\n    }\n    10%, 90% {\n        transform: translateX(-10%);\n    }\n    20%, 80% {\n        transform: translateX(-20%);\n    }\n    40%, 60% {\n        transform: translateX(-50%);\n    }\n}\n\n._low {\n    height: 55px;\n}\n\n.container {\n    background-color: $font_global-white;\n}\n\n.op2022 {\n    max-width: 1440px;\n    max-height: 1024px;\n    background-image: url('./images/main_picture.jpg');\n    width: 100vw;\n    height: 71.4vw;\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-end;\n    align-items: center;\n    margin: 0 auto;\n    background-size: auto;\n    background-position: bottom;\n    overflow: hidden;\n    \n    \n}\n\n\n\n.op2022_title {\n    margin: 0 auto;\n    justify-self: end;\n    display: flex;\n    flex-direction: column;\n    text-align: center;\n    z-index: 3;\n\n    .op2022__title__line1 {\n        font-family: $font-family_PalaceScriptMT;\n        font-size: 120px;\n        color: $font_golden_font;\n    }\n\n    .op2022__title__line2 {\n        font-family: $font-family_Mavel-Poster;\n        color: $font_global-white;\n        font-size: 80px;\n   